import React, { useState } from 'react';
import { createEvent } from '../../api/api'; //  API-

export default function CreateEvent() {
  const [formData, setFormData] = useState({
    title: '',
    date: '',
    time: '',
    seats: '',
    image: null,
    location: '',
    address: '',
    organizerPhone: '',
    host: '',
    maxParticipants: '',
    minParticipants: '',
    price: '',
    description: ''
  });

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleChange = (e) => {
    const { name, value, type, files } = e.target;
    setFormData({
      ...formData,
      [name]: type === 'file' ? files[0] : value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      //    API
      const result = await createEvent(formData);
      console.log(' :', result);
      //          
    } catch (err) {
      setError('   . ,  .');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="create-event">
      <h2> </h2>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="title"
            value={formData.title}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="date"
            name="date"
            value={formData.date}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="time"
            name="time"
            value={formData.time}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>- :</label>
          <input
            type="number"
            name="seats"
            value={formData.seats}
            onChange={handleChange}
            required
            min="1"
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="file"
            name="image"
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="location"
            value={formData.location}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="address"
            value={formData.address}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label> :</label>
          <input
            type="tel"
            name="organizerPhone"
            value={formData.organizerPhone}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="host"
            value={formData.host}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label> :</label>
          <input
            type="number"
            name="maxParticipants"
            value={formData.maxParticipants}
            onChange={handleChange}
            required
            min="1"
          />
        </div>
        <div className="form-group">
          <label> :</label>
          <input
            type="number"
            name="minParticipants"
            value={formData.minParticipants}
            onChange={handleChange}
            required
            min="1"
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <input
            type="text"
            name="price"
            value={formData.price}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>:</label>
          <textarea
            name="description"
            value={formData.description}
            onChange={handleChange}
            required
          />
        </div>
        <button type="submit" disabled={loading}>
          {loading ? '...' : ''}
        </button>
        {error && <p className="error-message">{error}</p>}
      </form>
    </div>
  );
}
