import React, { useState, useContext } from "react";
import { AuthContext } from "../../context/AuthContext";
import ConfirmationModal from "../Modal/ConfirmationModal";

const RegisterForm = ({ closeAuthModal }) => {
  const { registerUser } = useContext(AuthContext);
  const [name, setName] = useState("");
  const [subname, setSubname] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showModal, setShowModal] = useState(false);

  const handleRegister = async (e) => {
    e.preventDefault();

    if (password !== confirmPassword) {
      alert("  ");
      return;
    }

    try {
      await registerUser(name, subname, phone, password); //   
      setShowModal(true); //   
    } catch (error) {
      alert("  .  .");
      console.error(error);
    }
  };

  const handleConfirmation = () => {
    setShowModal(false);
    closeAuthModal(); //   
  };

  return (
    <>
      <form className="register-form" onSubmit={handleRegister}>
        <div className="auth-modal__input">
          <label htmlFor="reg-name"></label>
          <input
            id="reg-name"
            type="text"
            placeholder=""
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        <div className="auth-modal__input">
          <label htmlFor="reg-subname"></label>
          <input
            id="reg-subname"
            type="text"
            placeholder=""
            value={subname}
            onChange={(e) => setSubname(e.target.value)}
            required
          />
        </div>
        <div className="auth-modal__input">
          <label htmlFor="reg-phone"></label>
          <input
            id="reg-phone"
            type="tel"
            placeholder=""
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            required
          />
        </div>
        <div className="auth-modal__input">
          <label htmlFor="reg-password"></label>
          <input
            id="reg-password"
            type="password"
            placeholder=" "
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <div className="auth-modal__input">
          <label htmlFor="rep-password"></label>
          <input
            id="rep-password"
            type="password"
            placeholder=" "
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            required
          />
        </div>
        <div className="auth-modal__box reg">
          <button type="submit" className="_btn">
            
          </button>
        </div>
      </form>

      {showModal && (
        <ConfirmationModal
          onClose={() => setShowModal(false)}
          onConfirm={handleConfirmation}
        />
      )}
    </>
  );
};

export default RegisterForm;
